<template>
  <view class="content"> 
    <u-subsection 
        :list="list" 
        :current="current" 
        @change="sectionChange"
    ></u-subsection>
    <template v-if="current === 0">
        <view class="topTitle">
            <view class="topTitle_month">
                <text>8</text>
                月超重载设备数量
            </view>
            <view class="topTitle_tab" @click="handleShowDatetime">
                <u-datetime-picker
                    :show="showDatetime"
                    v-model="value1"
                    mode="datetime"
                ></u-datetime-picker>
                切换月份<text class="iconfont icon--xialajiantou"></text>
            </view>
        </view>
        <view class="inform">该分析截止至8月8号的数据</view>
        <view class="charts-box">
            <qiun-data-charts 
                type="area"
                :opts="opts"
                :chartData="chartData"
            />
        </view>
    </template>
    <template v-if="current === 1">
        2
    </template>
    <template v-if="current === 2">
        3
    </template>
    
  </view>
</template>

<script>
export default {
    data() {
        return {
            list: ["变压器","线路","负荷"],
            current: 0,
            showDatetime: false,
            chartData: {},
            opts: {
                color: ["#1890FF","#91CB74","#FAC858","#EE6666","#73C0DE","#3CA272","#FC8452","#9A60B4","#ea7ccc"],
                padding: [15,15,0,15],
                legend: {},
                xAxis: {
                    disableGrid: true
                },
                yAxis: {
                    gridType: "dash",
                    dashLength: 2
                },
                extra: {
                area: {
                    type: "curve",
                    opacity: 0.2,
                    addLine: true,
                    width: 2,
                    gradient: true
                }
                }
            }
        }
    },

    methods: {
        //获取顶部tab索引
        sectionChange(e) {
            this.current = e
        },

        //显示日期选择器
        handleShowDatetime() {
            this.showDatetime = true
        },

            getServerData() {
                //模拟从服务器获取数据时的延时
                setTimeout(() => {
                    //模拟服务器返回数据，如果数据格式和标准格式不同，需自行按下面的格式拼接
                    let res = {
                        categories: ["2016","2017","2018","2019","2020","2021"],
                        series: [
                        {
                            name: "成交量A",
                            data: [35,8,25,37,4,20]
                        },
                        {
                            name: "成交量B",
                            data: [70,40,65,100,44,68]
                        },
                        {
                            name: "成交量C",
                            data: [100,80,95,150,112,132]
                        }
                        ]
                    };
                    this.chartData = JSON.parse(JSON.stringify(res));
                }, 500);
            },
    },

    onReady() {
        this.getServerData();
    },
}
</script>

<style lang="scss">
    @font-face {
    font-family: "iconfont"; /* Project id 3600053 */
    src: url('//at.alicdn.com/t/c/font_3600053_6f7n2yfkj39.woff2?t=1660909170407') format('woff2'),
        url('//at.alicdn.com/t/c/font_3600053_6f7n2yfkj39.woff?t=1660909170407') format('woff'),
        url('//at.alicdn.com/t/c/font_3600053_6f7n2yfkj39.ttf?t=1660909170407') format('truetype');
    }

    .iconfont {
        font-family: "iconfont" !important;
        font-size: 16px;
        font-style: normal;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }

    .icon--xialajiantou:before {
        content: "\e616";
    }

    page {
        .content {
            padding: 20rpx;
            .topTitle {
                display: flex;
                justify-content: space-between;
                vertical-align: baseline;
                &_month {
                    flex: 1;
                    text {
                        font-size: 80rpx;
                        vertical-align: baseline;
                        color: $uni-bg-color-global;
                    }
                }
                &_tab {
                    flex:1;
                    text-align: right;
                    display: flex;
                    align-items: flex-end;
                    padding-bottom: 15rpx;
                }
            }
            .inform {
                color: #aaa;
            }
            .charts-box {
                margin-top: 60rpx;
                border-bottom: 2rpx solid #ccc;
            }
        }
    }
</style>